      thumbnail: "", //Ïç∏ÎÑ§Ïùº
      mark: 0, //Î∂ÅÎßàÌÅ¨ ÏïÑÏù¥ÏΩò
      isCollections: [], //Ïú†Ï†ÄÏùò Í∏∞Ï°¥ Ïª¨Î†âÏÖò,
