      rules: [
                value => !!value || 'Ïù¥Î¶ÑÏùÄ ÏûÖÎ†• Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //Ïù¥Î¶Ñ ÏóÜÏúºÎ©¥
                value => (value && value.length <= 10) || '10Í∏ÄÏûê Ïù¥ÎÇ¥Î°ú Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //10Í∏ÄÏûê Ïù¥ÎÇ¥Ïù¥Î©¥
                value => (this.catgNames.indexOf(value) == -1) || 'Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î¶ÑÏûÖÎãàÎã§üôè', //Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î¶ÑÏù¥Î©¥
                value => (this.checkSpace(value) == false) || 'Í≥µÎ∞±ÏùÄ ÏûêÏ†ú Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè', //Í≥µÎ∞±Ïù¥ ÏûàÏúºÎ©¥
