          confirmButtonColor: "#2255b1",
          cancelButtonColor: "#F36A3E",
          cancelButtonText: "Ï∑®ÏÜå",
          confirmButtonText: "ÎÑ§, Ï†ÑÌôòÌï†ÎûòÏöî!",
