          if (result.isConfirmed) {
            this.changeSecPost(postId);
            this.$swal("Ï†ÑÌôò ÏôÑÎ£å!", "ÎπÑÎ∞Ä Í≤åÏãúÍ∏ÄÎ°ú Ï†ÑÌôòÎêòÏóàÏäµÎãàÎã§!", "success");
          }
