        })
        .catch((err) => {
          alert("ÎπÑÎ∞ÄÍ∏ÄÏ†ÑÌôò Ïã§Ìå®" + err);
        });
    },


    //DOT LIST
    listBtn(i) {
      if (i == 0) {
        //Í≤åÏãúÍ∏Ä ÏàòÏ†ï
        console.log("ÏàòÏ†ïÌïòÍ∏∞");
        this.editPost();
      }
      if (i == 1) {
        //Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú
        console.log("ÏÇ≠Ï†úÌïòÍ∏∞");
        this.feedSwal(this.items.postId,1);
      }
      if (i == 2 && this.items.secPost == 0) {
        //ÎπÑÎ∞ÄÍ∏Ä Ï†ÑÌôò
        console.log("ÎπÑÎ∞ÄÍ∏ÄÎ°ú")
        this.feedSwal(this.postId,2);
      }
      if (i == 2 && this.items.secPost == 1) {
        //ÎπÑÎ∞ÄÍ∏Ä Ìï¥Ï†ú
        console.log("ÎπÑÎ∞ÄÍ∏Ä Ìï¥Ï†ú")
        this.changeSecPost(this.postId);
      }
    },

    
    //Ìï¥ÏãúÌÉúÍ∑∏ ÌÇ§ÏõåÎìú Í≤ÄÏÉâ
    search(e) {
      let getHashtag = e.target.innerText; //ÏÑ†ÌÉùÌïú Ìï¥ÏãúÌÉúÍ∑∏

      this.$router.push({ name: "snsmain", params: { detailHashtag : getHashtag },
      });
    },


    //Ï±ÑÌåÖÎ∞© Ïù¥Îèô
    send() {
      let vm = this;
      this.axios
        .get("/getSnsChatRoomNo", {
          params: { myId : this.$store.state.id, targetId : this.items.memberId },
        })
        .then(function (res) {
          console.log("res.data.vroomNo : " + res.data.vroomNo);
          vm.$router.push({
            name: "chat",
            query: { getRoomId: res.data.vroomNo },
          });
        })
        .catch(function (err) {
          console.log(err);
        });
    },

    
    //Ï¢ãÏïÑÏöî
    like(memberId, postId) {
      //Ï¢ãÏïÑÏöî ÏïåÎ¶º
      const noticeContent = {
        myId: this.$store.state.id,
        targetId: this.items.memberId,
        contentType: 0,
        postId: postId,
        likeStatus: this.items.likeStatus,
        noticeType: 0,
      };
      this.stompClient.send(
     
        "/app/Notice",
        JSON.stringify(noticeContent),
        (res) => {
          console.log("Ï¢ãÏïÑÏöî ÏïåÎ¶º Ï†ÑÏÜ°"),
          console.log(res);
        }
      );


      //Ï¢ãÏïÑÏöî Ïú†Ï†ÄÍ≤ÄÏ¶ù
      if(this.confirmMember(memberId) == false){
        this.loginConfirm();
      }else{
        //ÏÉÅÌÉúÍ∞íÏóêÎî∞Îùº Ï¢ãÏïÑÏöî or Ï∑®ÏÜåÏ≤òÎ¶¨
        this.axios
          .post("/sns/like", {
            targetId: postId,
            memberId: memberId,
          })
          .then((res) => {
            if (this.items.likeStatus == 0) {

              //Ï¢ãÏïÑÏöî ÏÉÅÌÉúÍ∞Ä ÏïÑÎãê Îïå
              ++this.items.likes;
              this.items.likeStatus = 1;
              console.log("Ï¢ãÏïÑÏöî ÏôÑÎ£å");
            } else if (this.items.likes > 0) {

              //Ï¢ãÏïÑÏöî ÏÉÅÌÉúÏùºÎïå
              --this.items.likes;
              this.items.likeStatus = 0;
              console.log("Ï¢ãÏïÑÏöî Ï∑®ÏÜå");
            }
          })
          .catch((err) => {
            console.log(err);
          });
       }
    },


    /*

      Î∂ÅÎßàÌÅ¨
    
      */


    //Î∂ÅÎßàÌÅ¨ Ï†Ñ Î°úÍ∑∏Ïù∏Í≤ÄÏ¶ù
    markLogin(memberId,e){
      if(this.confirmMember(memberId) == true && e==1){
        this.dialog2 = true;
      }else if(this.confirmMember(memberId) == true && e==2){
        Kakao.Share.sendDefault();
      }else{
        this.loginConfirm();
      }     
    },


    //Î∂ÅÎßàÌÅ¨ÏÉÅÌÉúÏ°∞Ìöå
    getBookmarkStatus(postId) {
      this.axios("sns/collection/bookmark/isBookmark/" + postId, {
        params: {
          memberId: this.memberId,
        },
      })
        .then((res) => {
          console.log("Î∂ÅÎßàÌÅ¨ÏÉÅÌÉú Ï°∞Ìöå ÏÑ±Í≥µ!");
          console.log("Î∂ÅÎßàÌÅ¨ÏÉÅÌÉú: "+res.data);
          this.mark = res.data; //Î≥ÄÏàòÏóê 0(Î∂ÅÎßàÌÅ¨X), 1(Î∂ÅÎßàÌÅ¨O)Ïù¥ Îã¥ÍπÄ
          console.log("Î∂ÅÎßàÌÅ¨ ÏÉÅÌÉú"+this.mark);
          
        })
        .catch((err) => {
          alert("Í≤åÏãúÍ∏ÄÌò∏Ï∂ú Ïã§Ìå®" + err);
        });
    },


    //Î∂ÅÎßàÌÅ¨
    bookmark(selectedCollection, postId) {
        this.axios
          .post("/sns/collection/bookmark", {
            catgId: selectedCollection,
            postId: postId,
            thumbnail: this.items.thumbnail,
          })
          .then((res) => {
            this.getBookmarkStatus(postId);
            this.dialog2 = false;
            console.log("Î∂ÅÎßàÌÅ¨ ÏÑ±Í≥µ!");
          })
          .catch((err) => {
            console.log(err);
          });
    },


    //Ïª¨Î†âÏÖò Î¶¨Ïä§Ìä∏ Ìò∏Ï∂ú
    getCollectionList(memberId) {

      this.axios("/sns/collection/" + memberId)
        .then((res) => {

          if(res.data.length == 0){ // ÎßåÎì§Ïñ¥ÎÜìÏùÄ Ïª¨Î†âÏÖòÏù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏúºÎ©¥ ÎîîÌè¥Ìä∏ Ïª¨Î†âÏÖò ÏÉùÏÑ±
                  this.createDefaultCollection(memberId);

          }else{ // ÎßåÎì† Ïª¨Î†âÏÖòÏù¥ Ï°¥Ïû¨ÌïòÎ©¥ Ïù¥Î¶ÑÎßå ÎΩëÏïÑÏÑú Î∞îÏù∏Îî©

              for(let i=0; i<res.data.length; i++){

                let isCatgName = res.data[i].catgName; 
                this.catgNames.push(isCatgName);

              }
                this.isCollections = res.data;
          }
        })
        .catch((err) => {
          alert("Ïª¨Î†âÏÖòÌò∏Ï∂ú Ïã§Ìå®" + err);
        });
    },


    //Î∂ÅÎßàÌÅ¨ ÏÇ≠Ï†ú
    bookmarkDel(postId, memberId) {
      this.axios
        .delete("/sns/collection/bookmark/" + postId + '/' + memberId)
        .then((res) => {
          this.getBookmarkStatus(postId);
          console.log("Î∂ÅÎßàÌÅ¨ ÏÇ≠Ï†ú ÏÑ±Í≥µ! " + res);
        })
        .catch((err) => {
          alert(err);
        });
    },


    //Ïª¨Î†âÏÖòÏÉùÏÑ±
    createCollection(memberId) {
      if (this.catgName == "" || this.catgName == undefined) {
        this.$swal("Ïª¨Î†âÏÖò Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöîüôè");
        return;
      }
      if (this.catgName.length > 11) {
        this.$swal("Ïù¥Î¶ÑÏùÄ 10Í∏ÄÏûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöîüôè");
        return;
      }
      this.dialog = !this.dialog;
      this.axios
        .post("/sns/collection", {
          memberId: memberId,
          catgName: this.catgName,
          thumbnail: "",
        })
        .then((res) => {
          // console.log(thumbnailImg);
          console.log(res);
          this.catgName = "";
          this.dialog3 = !this.dialog3;
          this.getCollectionList(memberId);

        })
        .catch((err) => {
          alert(err);
        });
    },


    //ÎîîÌè¥Ìä∏ Ïª¨Î†âÏÖò ÏÉùÏÑ±
    createDefaultCollection(memberId){
        const thumbnail = 'bookmark_default.png'

        this.axios.post('/sns/collection', {
            memberId : memberId,
            catgName : 'default',
            thumbnail : thumbnail,

        }).then(res => {
            this.getCollectionList(memberId);
        }).catch(err => {
            alert(err);
        });
      },


    //ÏÇ¨ÏßÑ ÎÑòÍ∏∞Í∏∞
    logic(e) {
      let currentMove = this.touch ? e.touches[0].clientX : e.clientX;
      if (this.move.length == 0) {
        this.move.push(currentMove);
      }
      if (this.move[this.move.length - 1] - currentMove < -100) {
        this.$refs.myCarousel.$el
          .querySelector(".v-window__prev")
          .querySelector(".v-btn")
          .click();
        this.drag = false;
        this.touch = false;
      }
      if (this.move[this.move.length - 1] - currentMove > 100) {
        this.$refs.myCarousel.$el
          .querySelector(".v-window__next")
          .querySelector(".v-btn")
          .click();
        this.drag = false;
        this.touch = false;
      }
    },


    //Ïú†Ï†Ä ÌîÑÎ°úÌïÑÎ°ú Ïù¥Îèô
    async goMyProfile(userId) {
      await this.getOneUser(userId);
      if (this.userOneInfo.role == 4) {
        this.$swal.fire("Í¥ÄÎ¶¨ÏûêÏóê ÏùòÌï¥ Ï†ëÍ∑º Í∏àÏßÄÎêú Ïú†Ï†Ä ÌîÑÎ°úÌïÑÏûÖÎãàÎã§.");
      } else {
         this.$router.push({ path: "/snsUserFeed", query: { userId: userId } });
      }
    },
     async getOneUser(userId) {
      const vm = this;
      await this.axios({
            url: "/member/" + userId,
            method: "get",
          })
          .then(function (response) {
            vm.userOneInfo = response.data;
          })
          .catch(function (error) {
            console.log(error);
          });
    },
  },
};

</script>
